const initialState = {
    roles:[],
    links:[{
      "path":"/home",
      "id":"HOME",
      "display":"â¿³",
      "permissions":[],
    },{
      "id":"SUPERADMIN",
      "display":"Superadmin",
      "permissions":["superadmin"],
      "menuItems":[{
        "path":"/users",
        "id":"USERS",
        "display":"Manage Users",
      }]
    }]
};

const reducer = (state = initialState, action) => {
  if(action.type === 'UPDATE_ROLES') {
    let newState = {...state};
    if(action.val.data && newState.roles.indexOf(action.val.data) === -1)
      newState.roles.push(action.val.data);
    
    let newLinks = [];
    newState.links.forEach((link)=>{
      if(link.permissions.length === 0)
        newLinks.push(link);
      else {
        let permissable = false;
        link.permissions.forEach((perm)=>{
          newState.roles.forEach((role)=>{
            if(perm === role)
              permissable = true;
          });
        });
        if(permissable)
          newLinks.push(link);
      }
    });
    
    newState.links = newLinks;
    
    return newState;
  }
  return state;
};


export default reducer;