import React from 'react';
import './LoginPage.scss';
import Spinner from '../../sharedComponents/functionalComponents/spinner/Spinner';
import Avatar from '@material-ui/core/Avatar';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { withRouter, Link } from 'react-router-dom';
import utils from '../../services/utils.js';
import { connect } from 'react-redux';
import axios from 'axios';

const mapDispatchToProps = (dispatch) => {
  return {
    updateRoles: (d) => dispatch({type:'UPDATE_ROLES', val:d})
  };
};

interface State {
  username:"",
  password:""
}

class LoginPage extends React.Component {
  public state:State;
  private cancelTokenSource:any;
  
  constructor(props) {
    super(props);
    this.state = {
      username:"",
      password:""
    };
  }
  
  componentDidMount() {
    localStorage.clear();
  }
  
  loginSubmit(e) {
    e.preventDefault();
    const url = "/api/authenticate";
   
    if(this.cancelTokenSource)
      this.cancelTokenSource.cancel();
    this.cancelTokenSource = axios.CancelToken.source();

    axios.post(url, {
      "username":this.state.username,
      "password":this.state.password
    }, {
      cancelToken: this.cancelTokenSource.token
    })
    .then((d)=>{
      const token = d.data.token;
      localStorage.setItem("authToken",token);
      
      let parsed = utils.parseJwt(token);
      if(parsed.role) {
        let p:any = this.props;
        p.updateRoles({ type: "UPDATE_ROLES", data: parsed.role});
      }
      
      const h:any = this.props;
      
      if(parsed.isEmailVerified)
        h.history.replace("/home");
      else
        h.history.replace("/verify");
    })
    .catch(function (error) {
      // handle error
      console.error(error);
    })
    .then(function () {
      // always executed
    });
  }
  
  render() {
    return (
      <section className="loginPage py-5">
       <div className="row">
        <div className="col-md-12">
          <h2>Log on</h2>
          <div className="loginFormContainer">
            <div className="text-center">
              <Avatar className="avatar" src="/public/user.png" />
            </div>
            <form onSubmit={(e)=>this.loginSubmit(e)} className="loginForm p-3">
              <div>
                <TextField required className="formField" value={this.state.username} onChange={(e)=>{this.setState({username:e.target.value})}} label="User name" variant="outlined" />
              </div>
              <div className="mt-3">
                <TextField required className="formField" value={this.state.password} onChange={(e)=>{this.setState({password:e.target.value})}} label="Password" type="password" variant="outlined" />
              </div>
              <div className="mt-3 text-right">
                <Button className="long-button" variant="contained" color="primary" type="submit">Log on</Button>
              </div>
            </form>
          </div>
          <p className="text-center">Forget <a href="#!">User name</a> or <a href="#!">Password</a></p>
        </div>
       </div>
      </section>
    );
  }
}
const cmp:any = LoginPage;
export default connect(null, mapDispatchToProps)(withRouter(cmp));