import React from 'react';
import './Header.scss';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Popover from '@material-ui/core/Popover';
import TextField from '@material-ui/core/TextField';
import styled, { ThemeProvider } from "styled-components";
import axios from 'axios';
import { withRouter, Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { compose } from 'redux';
import store from '../../../stores/store';
import utils from '../../../services/utils.js';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';


interface State {
  anchorEl:any,
  saEl:any,
  headerVisible:boolean
}

const mapStateToProps = (state) => {
  return {
    roles: state.roles,
    links: state.links
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateRoles: (d) => dispatch({type:'UPDATE_ROLES', val:d})
  };
};

class Header extends React.Component {
  state:State;
  private noHeaders = ["/", "/verify"];
  
  
  constructor(public props) {
    super(props);
    this.state = {
      anchorEl:null,
      saEl:null,
      headerVisible:false
    };
  }
  
  componentDidMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      console.log("on route change");
      this.setHeaderVisibility();
    });
    
    this.setHeaderVisibility();
    console.log("---------");
    console.log(this.props);
    
    
    const unsubscribe = store.subscribe(() => {
      console.log("********************");
      console.log("store updated");
      console.log(this.props.roles);
    });
    
    const token = localStorage.getItem("authToken");
    if(token) {
      let parsed = utils.parseJwt(token);
      if(parsed.role) {
        let p:any = this.props;
        p.updateRoles({ type: "UPDATE_ROLES", data: parsed.role});
      }
    }
  }
  
  setHeaderVisibility() {
    let path = this.props.location.pathname;
    
    if(this.noHeaders.indexOf(path) === -1)
      this.setState({headerVisible:true});
    else
      this.setState({headerVisible:false});
  }
  
  logout() {
    localStorage.clear();
    window.location.href = "/";
  }
  
  unlisten() {
    
  }
  
  changePageProgramatically(e) {
    e.preventDefault();
    this.props.history.push("/");
  }
  
  openPopover(el) {
    this.setState({
      anchorEl:el,
    });
  }
  
  openSA(el) {
    this.setState({
      saEl:el,
    });
  }
  
  closeSA() {
    this.setState({saEl:null});
  }
  
  generateMenuButton(d) {
    return <div>
    <button type="button" className="btn btn-outline-dark" onClick={(event)=>this.openSA(event.currentTarget)}>{d.display}</button>
    <Menu 
      anchorEl={this.state.saEl}
      open={Boolean(this.state.saEl)}
      onClose={(event)=>{this.closeSA();}}>
      
      {d.menuItems.map((item,index)=>{
        return <MenuItem onClick={(event)=>{this.closeSA();}}><Link to={item.path}>{item.display}</Link></MenuItem>
      })}
      
    
      
    </Menu>
    </div>;
  }
  
  render() {
    
    return (
        <header className={'mainHeader ' +(this.state.headerVisible ? " " : "hidden ")}>
          <div className="p-3">
            <div className="row align-items-center">
              <div className="col-md-12">
                <div className="row">
                  <nav className="col-auto linkWrapper">
                    <ul className="nav-pills pl-2 m-0">
                      {this.props.links && this.props.links.map((item,index)=>{
                        return <li className="nav-item pr-3 mb-1 mt-1" key={index}>
                          {item.path && <Link to={item.path}><button type="button" className="btn btn-outline-dark">{item.display}</button></Link>}
                          {!item.path && this.generateMenuButton(item)}
                        </li>
                      })}
                    </ul>
                  </nav>
                  <div className="text-right flex-grow-1 px-3">
                    <div className="avatarWrapper" onClick={(event)=>this.openPopover(event.currentTarget)}>
                      <Avatar className="avatarButton" src="/public/user.png" />
                    </div>
                    <Popover
                      anchorEl={this.state.anchorEl}
                      open={Boolean(this.state.anchorEl)}
                      onClose={(event)=>{this.setState({openLoginMenu:false,anchorEl:null});}}
                      anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                      }}
                      transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                      }}
                    >
                    <div className="list-group">
                      <div onClick={(event)=>this.logout()} className="list-group-item list-group-item-action list-group-item-light">Logout</div>
                    </div>
                    </Popover>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header));